<!--  -->
<template>
  <div
    class="shelf-wrapper"
    :class="{ 'search-top': showCancel, 'shadow-show': !shadowShow }"
  >
    <div class="shelf" ref="shelf" :class="{ 'shelf-move': showCancel }">
      <div class="shelf-search-input-wrapper">
        <div class="shelf-search-icon-wrapper">
          <span class="icon-search icon"></span>
        </div>
        <div class="shelf-search-input-wrapper-text">
          <input
            class="shelf-input"
            type="text"
            :placeholder="$t('shelf.search')"
            @click="searchClick"
            v-model="searchText"
            ref="searchInput"
          />
        </div>
        <div
          class="shelf-search-icon-cancel-wrapper"
          v-if="showCancelBtn"
          @click="cancelSearch"
        >
          <span class="icon-close-circle-fill icon"></span>
        </div>
      </div>
      <div
        class="shelf-search-icon-exchange"
        v-if="!showCancel"
        @click="exchange"
      >
        <span class="icon-cn" v-if="langCn"></span>
        <span class="icon-en" v-else></span>
      </div>
      <div
        class="shelf-search-cancel-wrapper"
        v-if="showCancel"
        @click="cancel"
      >
        <span class="cancel-text">{{ $t('shelf.cancel') }}</span>
      </div>
    </div>
    <transition name="hot-search-move">
      <div class="shelf-search-tab-wrapper" v-if="showCancel">
        <div
          class="shelf-search-tab-item"
          v-for="item in tabs"
          :key="item.id"
          @click="onTabClick(item.id)"
        >
          <span
            class="shelf-search-tab-text"
            :class="{ 'is-selected': item.id === selectedTab }"
            >{{ item.text }}</span
          >
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { setLocalStorage } from '../../utils/localStorage'
import { storeShelfMixin } from '../../utils/mixin'
// import { px2rem } from '../../utils/utils'
export default {
  mixins: [storeShelfMixin],
  data() {
    return {
      showCancel: false,
      langCn: true,
      searchText: '',
      showCancelBtn: false,
      selectedTab: 1,
      shadowShow: false,
    }
  },
  watch: {
    searchText(v) {
      if (v.length > 0) {
        this.showCancelBtn = true
      } else {
        this.showCancelBtn = false
      }
    },
    offsetY(v) {
      if (v > 0 && this.showCancel) {
        this.shadowShow = true
      } else {
        this.shadowShow = false
      }
    },
  },
  methods: {
    onTabClick(id) {
      this.selectedTab = id
    },
    searchClick() {
      this.showCancel = true

      // this.$refs.shelf.style.top = 0
      this.setShelfTitleVisible(false)
    },
    cancel() {
      this.showCancel = false
      this.searchText = ''
      // this.$refs.shelf.style.top = px2rem(42)
      this.setShelfTitleVisible(true)
    },
    cancelSearch() {
      this.searchText = ''
    },
    exchange(e) {
      if (e) {
        if (e.target.classList[0] === 'icon-cn') {
          this.$i18n.locale = 'en'
          this.langCn = false
        } else {
          this.$i18n.locale = 'cn'
          this.langCn = true
        }
        setLocalStorage('locale', this.$i18n.locale)
      }
    },
  },
  computed: {
    tabs() {
      return [
        {
          id: 1,
          text: this.$t('shelf.default'),
        },
        {
          id: 2,
          text: this.$t('shelf.progress'),
        },
        {
          id: 3,
          text: this.$t('shelf.purchase'),
        },
      ]
    },
  },
}
</script>
